import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {ITransferServiceProvider} from "../../providers/i-transfer-service/i-transfer-service";
import {GoogleAnalyticsProvider} from "../../providers/google-analytics/google-analytics";

@IonicPage()
@Component({
  selector: 'page-add-credit-account01',
  templateUrl: 'add-credit-account01.html',
})
export class AddCreditAccount01Page {

  formVal = {accountNo: ""};
  addCreditAccount01Error = "";
  accountNo;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public tokenService: TokenServiceProvider,
              public storage: LocalStorageProvider,
              public maskTokenUtil: MaskTokenProvider,
              public cryptoUtil: CryptoParamUtilProvider,
              public iTransferService: ITransferServiceProvider,
              public alertCtrl : AlertController,
              public gaProvider : GoogleAnalyticsProvider) {
  }

  AddNewCreditAccount01Action = function () {

    this.addCreditAccount01Error = "";
    let flag = true;
    this.accountNo=this.formVal.accountNo;

    if (this.accountNo == "" || typeof(this.accountNo) == "undefined") {
      this.addCreditAccount01Error = "Account No. can not be blank.";
      flag = false;
      return;
    }

    if (flag) {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }
  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let is2Factor="0";
      let appData = this.storage.retrieveFromStorage("appData");
      let encAccountNo = this.cryptoUtil.getEncryptVal(this.accountNo);
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      let encTwoFactor=this.cryptoUtil.getEncryptVal(is2Factor);
      this.iTransferService.addCreditAccount(unMaskToken, appData.userId, appData.appId, encAccountNo, encTwoFactor)
        .then(data => {
          this.CreditAccountAddComplete(data);
        });
    }
  };

  CreditAccountAddComplete = function (data) {

    if (data.statusCode == 1) {
      this.navCtrl.push('AddCreditAccount02Page', {'accDetails': JSON.stringify(data.object)});
    }

    else if (data.statusCode == 0) {
      this.addCreditAccount01Error = data.note;
    }

    else {
      //$log.info("else Loop");
      this.addCreditAccount01Error = "System Error Contact with Administrator.";
    }
  };

  ionViewDidLoad() {
    // console.log('ionViewDidLoad AddCreditAccount01Page');
    this.gaProvider.startTrackerWithId("Add Credit Account");
  }

}



